@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@800&family=Montserrat:wght@500&display=swap);
.main-div{
    position: relative;
    width: 100%;
    height: 100vh;
}

.box{
    width: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 50px;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
}

.box h1{
    margin-bottom: 30px;
    color: #fff;
    text-align: center;
}

.input-box{
    position: relative;
}

.input-box input{
    width: 100%;
    padding: 10px;
    font-size: 16px;
    color: #fff;
    letter-spacing: 1px;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
}

label{
    position: absolute;
    top: 0;
    left: 0;
    letter-spacing: 1px;
    padding: 10px 0;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.527);
    transition: 0.5s;
}

.box input[type="submit"]{
    background: transparent;
    border: none;
    outline: none;
    color: #fff;
    background: #03a9f4;
    padding: 8px 16px;
    border-radius: 5px;
    font-size: 14px;
}

.input-box input:focus ~ label, .input-box input:valid ~ label{
    top: -20px;
    left: 0;
    color: #03a9f4;
    font-size: 12px;
}

#log-in{
    cursor: pointer;
}
#unordered {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  position: -webkit-sticky;
  position: sticky;
}

.item {
  float: left;
}

.item a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.item a:hover {
  background-color: #111;
  text-decoration: none;
  color: white;
  font-weight: 800;
}

#logo img {
  width: 80px;
  height: 50px;
}

