.section {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
}
.section__image {
  width: 100%;
  height: 150vh;
  z-index: -1;
}

.main-footer {
  display: -webkit-flex;
  display: flex;
  color: white;
  background-color: black;
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
  padding: 70px;
}
.col-md-3 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.col-sm-6 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.footer-bottom {
  -webkit-align-items: center;
          align-items: center;
}
.copyright {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding-top: 20px;
}

.carousel {
  background-color: black;
  width: 100%;
  height: 100vh;
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
  display: -webkit-flex;
  display: flex;
  /* margin-top: 20px; */
}
.carouselInner {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: -webkit-flex;
  display: flex;
  /* top: 50px; */
  /* padding: 50px 0 0 50px; */
}

.carouselInner .left {
  -webkit-flex: 5% 1;
          flex: 5% 1;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.6);
  display: grid;
  place-items: center;
  color: white;
  cursor: pointer;
}
.carouselInner .center {
  -webkit-flex: 80% 1;
          flex: 80% 1;
  height: 100%;
  display: grid;
  place-items: center;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  text-align: justify;
}
.carouselInner .right {
  -webkit-flex: 5% 1;
          flex: 5% 1;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.6);
  display: grid;
  place-items: center;
  color: white;
  cursor: pointer;
}
.carouselInner h1 {
  font-size: 5rem;
  background-color: rgba(171, 206, 247, 0.8);
  border-radius: 15px;
  padding: 5px;
  margin-top: 40%;
  /* word-spacing: normal; */
}

.carouselInner p {
  font-size: 2rem;
  background-color: rgba(100, 235, 134, 0.8);
  border-radius: 15px;
  padding: 5px;
  /* word-spacing: normal; */
}

